                // position: [0, -20],
                color: '#ddd',
                formatter: (params) => {
                  let percent = 0;
                  let total = 0;
                  for (let i = 0; i < sexData.length; i++) {
                    total += sexData[i].value;
                  }
                  percent = ((params.value / total) * 100).toFixed(0);
                  if (params.name !== '') {
                    return params.name + ':' + percent + '%';
                  } else {
                    return '';
                  }
                },
              },
              labelLine: {
                length: 5,
                length2: 10,
                show: true,
                color: '#00ffff',
              },
            },
          },
          data: data,
        },
      ];

